@Composable
fun CheckEan() {
    var ean: String by remember { mutableStateOf("4006381333931") }
    H2 { Text("Validar el EAN 13") }
    P {
        Text("Pel que he entés,")
        Text(" sembla que és començar invertint la llista (index).")
        Text(" Llavors un suma tots el valors modul 10,")
        Text(" on els parells és multipliquen per 3 i els senars per 1 a excepció del 0 que serà el check!")
    }
    P {
        Text("Introdueix el EAN 13 a validar: ")
        Input(type = InputType.Text) {
            value(ean)
            onInput { event -> ean = event.value }
        }
    }
    P {
        if (ean.length != 13) {
            Text("Ha d'introduïr un EAN 13, aquest ha de tenir 13 caràcters i en té ${ean.length}!!")
        } else if (ean.any { c: Char -> !c.isDigit() }) {
            Text("Sembla que hi ha algún valor que no sigui dígit!")
        } else {
            val toCheck = ean.toList().map(Char::digitToInt).reversed().withIndex().reversed()
            Table {
                Tbody {
                    Tr {
                        Th { Text("Posició") }
                        toCheck.forEach { (i: Int, _) -> Td { Text("$i") } }
                    }
                    Tr {
                        Th { Text("Dígit") }
                        toCheck.forEach { (_, c: Int) -> Td { Text("$c") } }
                    }
                    Tr {
                        Th { Text("Vegades") }
                        toCheck.map { (i: Int, _) -> if (i % 2 == 0) 1 else 3 }.forEach { v: Int -> Td { Text("$v") } }
                    }
                    Tr {
                        Th { Text("Suma parcial") }
                        toCheck.map { (i: Int, v: Int) -> v * if (i % 2 == 0) 1 else 3 }
                            .forEach { v: Int -> Td { Text("$v") } }
                    }
                    Tr {
                        Th { Text("Acumulatiu modul 10") }
                        toCheck.map { (i: Int, v: Int) -> v * if (i % 2 == 0) 1 else 3 }
                            .cumulativeSum()
                            .forEach { v: Int -> Td { Text("$v") } }
                    }
                }
            }
            Text("Serà valid si acaba amb 0")
            P {
                if (checkEan13(ean)) {
                    Text("EAN 13 '$ean' validat!")
                } else {
                    Text("EAN 13 '$ean' no validat!")
                }
            }
        }
    }
    if (ean.length == 13 && ean.all { c: Char -> c.isDigit() }) {
        CanvasEan(ean)
    }
}


@Composable
fun CanvasEan(ean: String) {
    val eanInt: List<Int> = ean.map(Char::digitToInt)
    val first: Int = eanInt.first()
    H2 { Text("Visualitzar el EAN 13") }
    H3 { Text("La codificació del $ean") }
    P {
        Text("Considerem que hi ha 95 àrees.")
        Text(" Comença i acaba amb 101.")
        Text(" Al mig hi ha un 01010.")
    }
    H4 { Text("Primer dígit") }
    P {
        Text("No apareix directament amb codi de barres, ")
        Text(" però si en la codificació dels següents 42 bits.")
    }
    P {
        Text("$first: ${ean13FirstDigit[first]} and [R, R, R, R, R, R]")
    }
    H4 { Text("A binary") }
    val binaryList: List<Boolean> = eanToBin(ean.toList())
    P {
        val booleans = binaryList.map { if (it) '1' else '0' }.joinToString("")
        Text(booleans)
    }
    H4 { Text("To Canvas!") }
    var canvasRef: HTMLCanvasElement? by remember { mutableStateOf(null) }

    val width: Int = 95 * 3
    val height: Int = (95 * 3 / 1.618f).toInt()
    Canvas(
        attrs = {
            style {
                width(width.px)
                height(height.px)
                //border(1.px, LineStyle.Solid, Color.blue)
                margin(20.px)
            }
            ref {
                canvasRef = it
                onDispose { canvasRef = null }
            }
        }
    )

    LaunchedEffect(canvasRef) {
        canvasRef?.let { canvas ->
            val ctx = canvas.getContext("2d") as CanvasRenderingContext2D
            ctx.fillStyle = "black"
            binaryList.withIndex().filter { (_, b: Boolean) -> b }
                .forEach { (i, b) ->
                    ctx.fillRect(x = i * 3.0, y = 0.0, w = 3.0, h = height.toDouble())
                }
            ctx.stroke()
        }
    }
}

private fun eanToBin(eanChar: List<Char>): List<Boolean> {
    val codification = ean13FirstDigit[eanChar.first().digitToInt()]
    val (firstElements, lastElements) = eanChar.drop(1).chunked(6)

    return listOf(true, false, true) +
            (firstElements zip codification).flatMap { p -> ean13EncodingLGR.getValue(p) } +
            listOf(false, true, false, true, false) +
            lastElements.flatMap { c: Char -> ean13EncodingLGR.getValue(Pair(c, Ean13Encoding.R)) } +
            listOf(true, false, true)
}

enum class Ean13Encoding {
    L, G, R;

    companion object {
        fun of(c: Char): Ean13Encoding = entries.associateBy { a -> a.name.toCharArray().first() }.getValue(c)
    }
}

val ean13FirstDigit: List<List<Ean13Encoding>> = listOf(
    "LLLLLL",
    "LLGLGG",
    "LLGGLG",
    "LLGGGL",
    "LGLLGG",
    "LGGLLG",
    "LGGGLL",
    "LGLGLG",
    "LGLGGL",
    "LGGLGL",
).map { code: String -> code.toList().map { c: Char -> Ean13Encoding.of(c) } }

val ean13EncodingLGR: Map<Pair<Char, Ean13Encoding>, List<Boolean>> = listOf(
    "0001101",
    "0011001",
    "0010011",
    "0111101",
    "0100011",
    "0110001",
    "0101111",
    "0111011",
    "0110111",
    "0001011",
).map { str: String ->
    str.toList().map { c: Char ->
        when (c) {
            '0' -> false
            '1' -> true
            else -> error("Ups ? $c !∈ [0, 1]")
        }
    }
}.mapIndexed { i: Int, L: List<Boolean> ->
    val R: List<Boolean> = L.map { b: Boolean -> b.not() }
    val G: List<Boolean> = R.reversed()
    listOf(
        NumberCode(i, Ean13Encoding.L, L),
        NumberCode(i, Ean13Encoding.G, G),
        NumberCode(i, Ean13Encoding.R, R)
    )
}.flatten().associate { nc: NumberCode -> Pair(nc.digit.digitToChar(), nc.type) to nc.code }

private data class NumberCode(val digit: Int, val type: Ean13Encoding, val code: List<Boolean>)

private fun checkEan13(input: String): Boolean = input.reversed().map(Char::digitToInt)
    .reduceIndexed { i: Int, acc: Int, element: Int -> acc + element * if (i % 2 == 0) 1 else 3 } % 10 == 0

private fun List<Int>.cumulativeSum(): List<Int> {
    var acc = 0
    return this.map { element: Int ->
        acc += element
        acc %= 10
        acc
    }
}
