package com.cucarell.web.components.widgets

import androidx.compose.runtime.Composable
import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import androidx.compose.runtime.setValue
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.padding
import com.varabyte.kobweb.compose.ui.toAttrs
import org.jetbrains.compose.web.attributes.InputType
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.Input
import org.jetbrains.compose.web.dom.Text

@Composable
fun reverseText(modifier: Modifier = Modifier) {
    var inputText by remember { mutableStateOf("") }

    Row(modifier.padding(16.px)) {
        Text("Enter text to reverse:")
        Input(InputType.Text, attrs = Modifier.toAttrs {
            onChange { inputText = it.value }
        })
        Text("output: ${inputText.reversed()}")
    }
}
