package com.cucarell.web.pages.blog.y2025.w12

import androidx.compose.runtime.*
import com.varabyte.kobweb.core.*
import com.varabyte.kobwebx.markdown.*

@Page("/blog/y2025/w12/")
@Composable
fun KobwebPage() {
    CompositionLocalProvider(LocalMarkdownContext provides MarkdownContext("blog/y2025/w12/Kobweb.md", mapOf("title" to listOf("Aprenent a usar Kobweb"), "author" to listOf("Arnau Sistach Reinoso")))) {
        com.varabyte.kobweb.compose.foundation.layout.Column {
            org.jetbrains.compose.web.dom.H1(attrs = { id("markdown") }) {
                org.jetbrains.compose.web.dom.Text("Markdown")
            }
            org.jetbrains.compose.web.dom.P {
                org.jetbrains.compose.web.dom.Text("Intentant fer un ")
                org.jetbrains.compose.web.dom.Code {
                    org.jetbrains.compose.web.dom.Text("markdown")
                }
                org.jetbrains.compose.web.dom.Text(" amb scripts de ")
                org.jetbrains.compose.web.dom.Code {
                    org.jetbrains.compose.web.dom.Text("JavaScript")
                }
                org.jetbrains.compose.web.dom.Text(" però implementat amb ")
                org.jetbrains.compose.web.dom.Code {
                    org.jetbrains.compose.web.dom.Text("Kotlin")
                }
                org.jetbrains.compose.web.dom.Text(". Ho farem amb un exemple senzill, invertir el text introduït:")
            }
            com.cucarell.web.components.widgets.reverseText()
            org.jetbrains.compose.web.dom.P {
                org.jetbrains.compose.web.dom.Text("Exageradament agraït que tot això pugui estar funcionant.")
            }
            org.jetbrains.compose.web.dom.H1(attrs = { id("millores") }) {
                org.jetbrains.compose.web.dom.Text("Millores")
            }
            org.jetbrains.compose.web.dom.P {
                org.jetbrains.compose.web.dom.Text("Millores que desitjo fer:")
            }
            org.jetbrains.compose.web.dom.Ul {
                org.jetbrains.compose.web.dom.Li {
                    org.jetbrains.compose.web.dom.Text("L'escript que es genera actualment és un ")
                    org.jetbrains.compose.web.dom.Code {
                        org.jetbrains.compose.web.dom.Text("web.js")
                    }
                    org.jetbrains.compose.web.dom.Text(", però jo desitjo que sigui un ")
                    org.jetbrains.compose.web.dom.Code {
                        org.jetbrains.compose.web.dom.Text("cucarell.js")
                    }
                    org.jetbrains.compose.web.dom.Text("!!")
                }
                org.jetbrains.compose.web.dom.Li {
                    org.jetbrains.compose.web.dom.Text("Automaticament, al fer ")
                    org.jetbrains.compose.web.dom.Code {
                        org.jetbrains.compose.web.dom.Text("kobweb run")
                    }
                    org.jetbrains.compose.web.dom.Text(" sapiga que és estàtica la web.")
                }
            }
        }
    }
